.App {
  text-align: center;
  font-family: 'Special Elite', cursive;
}

/* --------------------------------------------------------- */
.info {
	background: #c9c1ac;
}

body {
	font-family: "Special Elite", cursive;
	color: whitesmoke;
	background: url(./components/img/itembackground.jpg) center/cover fixed no-repeat;
	flex-wrap: wrap;
}

.primary-color {
	color: #e77728;
}

.color-color {
	color: whitesmoke;
}

.info h2 {
	word-spacing: 0.6rem;
}

.info a {
	transition: color 2s ease-in-out;
}

info a:hover {
	color: #d2d7df;
}

/* header */
#header {
	min-height: calc(100vh - 90px);
	background: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
		url(./components/img/background_4.jpg);
	background-position: center;
	background-size: cover;
	background-attachment: fixed;
	background-repeat: no-repeat;
	position: relative;
}

.height-90 {
	min-height: calc(100vh - 90px);
}

.banner h1 {
	border-top: 6px dotted #d2d7df;
	border-bottom: 6px dotted #d2d7df;
	animation: fromLeft;
	animation-duration: 4s;
	animation-delay: 2s;
	opacity: 0;
	animation-fill-mode: forwards;
}

@keyframes fromLeft {
	0% {
		transform: translateX(-100px);
		opacity: 0;
	}
	50% {
		transform: translateX(30px);
	}
	100% {
		transform: translateX(0);
		opacity: 1;
	}
}

.main-btn {
	color: #e77728;
	font-size: 2rem;
	border: 2px solid #d2d7df;
	transition: all is ease-in-out;
}

.main-btn:hover {
	color: #242424;
	border: 2px solid #c9c1ac;
	background: #c9c1ac;
}

.estratweni-btn {
	animation: fromRight;
	animation-duration: 4s;
	animation-delay: 2s;
	opacity: 0;
	animation-fill-mode: forwards;
}

@keyframes fromRight {
	0% {
		transform: translateX(100px);
		opacity: 0;
	}
	50% {
		transform: translateX(-30px);
	}
	100% {
		transform: translateX(0);
		opacity: 1;
	}
}

.header-link {
	display: inline-block;
	position: absolute;
	bottom: 5%;
	left: 50%;
	background: #c9c1ac;
	border-radius: 10px;
	transition: all 2s ease;
	animation: fromBottom;
	animation-duration: 4s;
	animation-iteration-count: infinite;
}

.header-link:hover {
	color: #242424;
}
@keyframes fromBottom {
	0% {
		transform: translateY(-10px);
	}
	50% {
		transform: translateY(10px);
	}
	100% {
		transform: translateY(-10px);
	}
}

/* navbar */
.navbar {
	background: #c9c1ac;
	padding: 20px 20px;
	transition: all 1s ease;
}
.navbar-background {
    padding: 5px 20px;
    position: "fixed-top";
}
.navbar-brand {
	font-size: 30px;
	transition: all 1s ease;
}
.navbar-brand:hover {
	color: #242424;
}
.nav-link {
	color: #242424;
	text-decoration: none;
	transition: all 2s ease;
	font-size: 23px;
}
.nav-link:hover {
	color: #e77728;
}

.nav-btn {
	background: transparent;
	color: #e77728;
	border: 2px solid #e77728;
	font-size: 23px;
	transition: all 2s ease;
}
.nav-btn:hover {
	color: #242424;
	border: 2px solid #242424;
	transform: rotate(10deg);
}
.toggler-btn {
	border: 2px solid #e77728;
	padding: 5px;
	transition: all 2s ease;
}
.bar {
	width: 30px;
	height: 3px;
	margin: 5px;
	background: #e77728;
	transition: all 0.5s ease;
}

.change .bar1 {
	transform: rotate(-45deg) translate(-5px, 6px);
}

.change .bar2 {
	opacity: 0;
}

.change .bar3 {
	transform: rotate(45deg) translate(-5px, -8px);
}

/* menu items */
.item-container {
	position: relative;
	/* for the link */
	overflow: hidden;
	background: linear-gradient(rgb(0, 0, 0), rgb(0, 0, 0));
	transition: all 2s ease;
}

.item-img {
	transition: all 2s ease;
}

.item-container:hover .item-img {
	transform: scale(1.2);
	opacity: 0.5;
}

.item-link {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	color: #e77728;
	font-size: 2rem;
	border-top: 3px solid #e77728;
	border-bottom: 3px solid #e77728;
	transition: all 2s ease;
	opacity: 0;
}

.item-container:hover .item-link {
	opacity: 1;
}

.item_link:hover {
	color: #c9c1ac;
	border: 3px solid #c9c1ac;
	background: #c9c1ac;
	color: #242424;
}

.item-container:hover {
	outline: 10px solid #e77728;
	outline-offset: -0.8rem;
}

/* menu */

#menu {
	background: linear-gradient(
			rgba(255, 255, 255, 0.7),
			rgba(255, 255, 255, 0.7)
		),
		url(./components/img/itembackground_2.jpg) center/cover fixed no-repeat;
}


/* about */

.about-pictures {
	position: relative;
}
.about-image {
	position: absolute;
	width: 150px;
	transition: all 2s ease;
	opacity: 0.9;
}

.img-1 {
	left: 0%;
	top: 0%;
}
.img-2 {
	left: 20%;
	top: 20%;
	z-index: 10;
}
.img-3 {
	left: 40%;
	top: 45%;
	z-index: 20;
}
.img-4 {
	left: 40%;
	top: 0%;
}
.img-5 {
	left: 0%;
	top: 45%;
}
.img-6 {
	left: 60%;
	top: 20%;
}
#about {
	background: linear-gradient(
			rgba(255, 255, 255, 0.7),
			rgba(255, 255, 255, 0.7)
		),
		url(./components/img/itembackground_2.jpg) center/cover fixed no-repeat;
}
/* #about .text {
    background: #e77728;
    border-color: #e77728;
} */
.about-image:hover {
	outline: 10px solid #e77728;
	outline-offset: 0.8rem;
	transform: scale(1.3) rotate(20deg);
	z-index: 30;
	opacity: 1;
}
.about-pictures:hover .about-image:not(:hover) {
	opacity: 0.5;
}

/* review */
.review-item {
	background: rgba(223, 166, 166, 0.7);
	background-color: #e77728;
	clip-path: polygon(5% 0, 100% 0, 90% 100%, 0 100%);
}

.reviews-img {
	width: 350px;
	height: 350px;
}
.lead-text {
    color: black;
}

/* team */
.team-icons a {
	color: #3b5998;
	transition: all 2s ease-in-out;
}
.card-title {
	color: #606060;
	transition: all 2s ease-in-out;
}

.team-icons a:hover {
	color: #242424;
	transform: translateY(-5px);
}

/* contact */
.height-60 {
	min-height: 60vh;
}

#contact .card {
	background: rgba(0, 0, 0, 0.5);
	transition: all 2s ease;
}

#contact .card:hover {
	background: rgba(0, 0, 0, 0.8);
}

#contact .card-header {
	color: #e77728;
}

#contact .input-group-text {
	background: #e77728;
	border-color: #e77728;
	columns: #242424;
}

#contact .card input {
	color: #606060;
}

#contact .card-input::placeholder {
	color: #606060;
}

.contact-btn {
	color: #e77728;
	border-color: #e77728;
	background: transparent;
	transition: all 2s ease-in-out;
}

.contact-btn:hover {
	background: #c9c1ac;
	color: #e77728;
	border-color: #c9c1ac;
}

#contact {
	background: linear-gradient(
			rgba(255, 255, 255, 0.7),
			rgba(255, 255, 255, 0.7)
		),
		url(./components/img/logo.jpg) center/cover fixed no-repeat;
}

/* footer */
#back-to-top {
	background: #c9c1ac;
	border-radius: 10px;
	display: inline-block;
	
}
#footer {
    height:115px;
}
.scrollTop {
	position: fixed;
	bottom: 70px;
	right: 10px;
}

@viewport {
  width: device-width ;
  zoom: 1.0 ;
}
.modal img{
	width: 320px;
}

img { max-width: 100%; height: auto; }


    
        @media (max-width: 500px) {
            .reviewsl-container {
                text-align: center;
            }
    
            #reviews-container img {
                margin: auto;
                float: none;
                display: block;
            }
        }